.TodoForm-box {
  width: 440px;
  height: 200px;
  margin-left: 8px; 
  display: block;
  align-items: center;
  justify-content: center;
  border: solid 1px black;
  border-radius: 6px;
  background-color: dimgray;
}

.TodoForm-label {
  color: white;
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 28px;
  text-align: left;
}

.TodoForm-textarea {
  width: 385px;
  height: 100px;
  margin-bottom: 10px;
}

.TodoForm-buttons--div {
  display: flex;
  justify-content:space-around;
  align-items: center;
}

.TodoForm-button--cancel {
  width: 70px;
  height: 25px;
  background-color: rgb(255, 64, 64);
  color: white;
  border: 1px;
  border-radius: 2px;
}

.TodoForm-button--add {
  width: 70px;
  height: 25px;
  background-color: rgb(0, 98, 128);
  color: white;
  border: 1px;
  border-radius: 2px;
}

@media (hover: hover) {
  .TodoForm-button--cancel:hover {
    border: solid rgb(255, 64, 64) 1px;
    color: rgb(255, 64, 64);
    background-color: white;
  }

  .TodoForm-button--add:hover {
    border: solid rgb(0, 98, 128) 1px;
    color: rgb(0, 98, 128);
    background-color: white;
  }

}